.prank-buttons-container {
  position: relative;
  height: 100vh; /* Cover whole page */
}

.background-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://img.freepik.com/free-vector/cute-valentines-day-background_23-2148043323.jpg?w=1800&t=st=1707754773~exp=1707755373~hmac=d7ae08ab6c6c5594845c821c58e0f42c1807eab1d469f26825703553c723bed8'); /* Replace 'background-image.jpg' with your image URL */
  background-size: cover;
  opacity: 0.75; /* 75% opacity */
}

.prank-buttons {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  z-index: 1; /* Ensure buttons are above the overlay */
}

.button-container {
  margin-top: 20px;
  text-align: center; /* Center-align buttons */
}

button {
  padding: 10px 20px;
  font-size: 16px;
  margin-right: 10px;
  cursor: pointer;
}

.yes-button {
  background-color: #87CEEB;
  color: white;
}

.no-button {
  background-color: #2CCC92;
  color: white;
}
